.header__box {
  justify-content: space-between;
  display: flex;
}

.header__logo {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--second-title-color);
  align-items: center;
  font-family: Titan One, cursive;
  font-size: 26px;
  font-weight: 400;
  line-height: 1.46;
  display: flex;
}

@media screen and (min-width: 480px) {
  .header__logo {
    font-size: 36px;
