.mobile-menu {
  z-index: 2;
  height: 100vh;
  width: 75%;
  transition: transform var(--transition-duration) var(--transition-timing-function);
  background-color: #e17992;
  padding: 60px 20px 20px;
  position: fixed;
  top: 0;
  right: 0;
  overflow-x: scroll;
  transform: translateX(100%);
}

