.wobble-hor-bottom {
  animation: wobble-hor-bottom 1s both;
}

@keyframes wobble-hor-bottom {
  0%, 100% {
    transform-origin: 50%;
    transform: translateX(0%);
  }

  15% {
    transform: translateX(-30px)rotate(-6deg);
  }

  30% {
    transform: translateX(15px)rotate(6deg);
